          <p>И, открыв двери театров и концертных залов, надо будет заново прислушиваться к тому, что волнует сегодняшнего зрителя, чего он ждет от всех, кто сочиняет романы, сценарии, пьесы, ставит спектакли и снимает фильмы. Нам самим надо будет ощутить и понять, насколько новый опыт, полученный за последние месяцы, подтолкнет нас к новым художественным решениям. Или позволит сохраниться в пределах привычного.</p>
