import React from 'react'
import { useHistory } from 'react-router-dom'
import styles from './Main.module.css'

import { ReactComponent as RibbonHotelSVG } from '../../assets/svg/ribbon_hotel.svg'
import { ReactComponent as Rhombus } from '../../assets/svg/rhombus.svg'
import { ReactComponent as CornerOpacity} from '../../assets/svg/corner_opacity.svg'

import manPicture from '../../assets/images/man.png'

export default function Main() {
  const history = useHistory()

  // Handlers
  const enterHandler = () => {
    history.push('/hotel')
  }

  return (
    <div className={styles.root}>
      <div className={styles.man}>
        <div className={styles.manTable}></div>
        <img className={styles.manPicture} src={manPicture} alt=''/>
      </div>
      <div className={styles.rootInner}>
        <div className={styles.ribbonHotel}>
          <RibbonHotelSVG/>
        </div>
        {/* Begin ribbonYear */}
        <div className={styles.ribbonYear}>
          <div className={styles.ribbonYearText}>Нам</div>
          <div className={styles.ribbonYearValue}>30</div>
          <div className={styles.ribbonYearText}>лет</div>
        </div>
        {/* Begin ribbonEnter */}
        <div className={styles.ribbonEnter}>
          <div className={styles.ribbonEnterText} onClick={enterHandler}>
            Зайти в&nbsp;гости
          </div>
          <div className={styles.lineBox}>
            <div className={styles.lineBoxDot}>
              <Rhombus/>
            </div>
          </div>
        </div>
        <div className={styles.gotoRG}>
          <a rel="noopener noreferrer" href="//rg.ru" target="_blank">Почитать новости RG.RU</a>
        </div>
      </div>

      {/* Begin borderLayer */}
      <div className={styles.borderLayer}>
        <i className={`${styles.borderLayerLine} ${styles.lineTop}`}></i>
        <i className={`${styles.borderLayerLine} ${styles.lineBottom}`}></i>
        <i className={`${styles.borderLayerLine} ${styles.lineLeft}`}></i>
        <i className={`${styles.borderLayerLine} ${styles.lineRight}`}></i>
        <b className={`${styles.corner} ${styles.cornerTop} ${styles.cornerLeft}`}>
          <CornerOpacity/>
        </b>
        <b className={`${styles.corner} ${styles.cornerTop} ${styles.cornerRight}`}>
          <CornerOpacity/>
        </b>
        <b className={`${styles.corner} ${styles.cornerBottom} ${styles.cornerLeft}`}>
          <CornerOpacity/>
        </b>
        <b className={`${styles.corner} ${styles.cornerBottom} ${styles.cornerRight}`}>
          <CornerOpacity/>
        </b>
      </div>
    </div>
  )
}